export default {
	components: {
	},
	props: [
		'value',
		'type',
		'isPassword',
		'placeholder',
		'placeholderStyle',
		'placeholderClass',
		'disabled',
		'cursorSpacing',
		'focus',
		'confirmType',
		'confirmHold',
		'cursor',
		'adjustPosition',
		'maxlength',
		'inputColor',
		'inputFontSize',
		'inputLineHeight',
		'inputLine',
		'inputWidth',
		'inputHeight',
		'inputTAlign',
		'inputPadding',
		'inputBG',
		'inputBorder',
		'inputBStyle',
		'indexNum',
		'inputBoxMargin',
		'inputLineStyle',
		'inputLineYShow',
		'inputSortShow',
		],
	data() {
		return {
			inputMargin: '1px',
			inputBGs: '', // 默认
			inputBorders: '', // 默认
			inputBStyles: '', // 默认
			valueText: this.value
		};
	},
	created() {
		if(this.inputBoxMargin){
			this.inputMargin = this.inputBoxMargin;
		}
	},
	watch: {
		value: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------监听valueText变化---------------------------------------');
				this.valueText = a;
			},
			deep: true
		}
	},
	methods: {
		// 当键盘输入时，触发input事件
		onInput(e) {
			console.log(e, 'input返回值');
		},
		// 输入框聚焦时触发
		onFocus(e) {
			if (this.disabled === true) {
				return
			}
			console.log(e, 'input返回值');
			if(!this.inputBoxMargin) { // 如果不存在用户自定义样式
				this.inputMargin = '';
				this.inputBGs = this.inputBG;
				this.inputBorders = this.inputBorder;
				this.inputBStyles = this.inputBStyle;
			}
		},
		// 输入框失去焦点时触发
		onBlur(e) {
			console.log(e, 'input返回值');
			this.valueText = e.detail.value;
			this.$emit('tap-Input',this.valueText);
			if(!this.inputBoxMargin) { // 如果不存在用户自定义样式
				this.inputMargin = '1px';
				this.inputBGs = '';
				this.inputBorders = '';
				this.inputBStyles = '';
			}
			uni.pageScrollTo({
			scrollTop: 0,
			duration: 0
			});
		},
		// 点击完成按钮时触发
		onConfirm(e) {
			console.log(e, 'input返回值');
			this.valueText = e.detail.value;
			this.$emit('tap-Input',this.valueText);
			if(!this.inputBoxMargin) { // 如果不存在用户自定义样式
				this.inputMargin = '1px';
				this.inputBGs = '';
				this.inputBorders = '';
				this.inputBStyles = '';
			}
			uni.pageScrollTo({
			scrollTop: 0,
			duration: 0
			});
		},
		
	}
};
